    <div class="flex items-center w-full max-w-[76rem] justify-end gap-6">

      <button v-if="contacts.length > 0" class="btn-create" @click="showFormRegister">
        <icon-component name="plus" /> Criar contato 
      </button>
  
      <form 
        @submit="submitToSearch"
        id="search-contacts">
        <input 
          type="search" 
          name="search-contact"
          v-model="search"
          placeholder="Buscar..." 
          @search="goToSearch" />
          <button type="submit">
            <icon-component name="search" />
          </button>
      </form>
    </div>
  </header>
</template>

<script>

  import store from '../vuex/store';

  export default {
    data(){
      return{
        search : null
      }
    },
    mounted() {
      this.search = store.getters.paramsSearch;
    },
    computed:{
      contacts : function(){
        return store.getters.contacts;
      }
    },
    methods:{
      submitToSearch(e){
        e.preventDefault();
        this.goToSearch();
      },
      goToSearch(){ store.dispatch('searchContacts',this.search) },
      showFormRegister(){
        this.$parent.$refs.listContacts.showForm();
      }
    }
  }
</script>